.gridWrapper {
    display: grid;
    grid-template-areas: 'avatar info posts';
    grid-template-columns: repeat(3, 1fr);
}

.avatar {
    grid-area: avatar;
}

.profileInfo {
    grid-area: info;
}

.posts {
    grid-area: posts;
    text-align: center;
}

.hintInfo {
    font-size: 0.8em;
    color: rgb(71, 76, 255);
}